import axios from 'axios';
import Cropper from 'cropperjs';

class EconaCustomField {

  constructor(options) {
    const defaults = {};
    this.params = Object.assign({}, defaults, options || {});

    this.container = this.params.container;
    this.econa = this.container.querySelector('.econa-container');
    this.cropperContainer = this.container.querySelector('.econa-cropper-container');
    this.cropperImage = this.container.querySelector('.econa-cropper-image');
    this.previewImage = this.container.querySelector('.econa-preview-image');
    this.editButton = this.container.querySelector('.econa-toolbar [data-action="edit"]');
    this.deleteButton = this.container.querySelector('.econa-toolbar [data-action="delete"]');
    this.cropperButtons = this.container.querySelector('.econa-cropper-buttons');

    this.aspectRatioField = this.container.querySelector('.econa-aspect-ratio-field');
    this.uploadField = this.container.querySelector('.econa-upload-field');
    this.deleteField = this.container.querySelector('.econa-delete-field');
    this.contextField = this.container.querySelector('.econa-context-field');
    this.fileField = this.container.querySelector('.econa-file-field');
    this.filenameField = this.container.querySelector('.econa-filename-field');
    this.valueField = this.container.querySelector('[data-econa]');

    this.data = JSON.parse(this.valueField.value) || {};
    this.key = this.valueField.dataset.econa;

    this.validatorField = this.container.querySelector('[data-role="econa-validator"]');

    this.pathField = this.container.querySelector('.econa-path-field');
    this.captionField = this.container.querySelector('.econa-caption-field');
    this.creditsField = this.container.querySelector('.econa-credits-field');
    this.altField = this.container.querySelector('.econa-alt-field');
    this.options = JSON.parse(this.container.querySelector('.econa-params').textContent);

    this.cropper = null;
    this.modal = null;

    this.start();

  }

  start() {
    this.createModal();
    this.updateValue();
    this.addEvents();
    if (!this.data.filename) {
      this.disableToolbarButtons();
    }
  }

  updateValue() {
    this.data.key = this.key;
    this.data.caption = this.captionField.value;
    this.data.credits = this.creditsField.value;
    this.data.alt = this.altField.value;
    this.valueField.value = JSON.stringify(this.data);
  }

  addEvents() {

    // Update the hidden field value when a field is changed
    const textFields = this.container.querySelectorAll('input[type="text"]');
    for (let i = 0; i < textFields.length; i++) {
      textFields[i].addEventListener('change', () => {
        this.updateValue();
      });
    }

    // Upload when file is selected
    this.fileField.addEventListener('change', () => {
      this.upload({
        file: this.fileField.files[0]
      });
    });

    // Upload when image is selected from media manager
    let currentValue = this.pathField.value;
    if ('MutationObserver' in window) {
      const observer = new MutationObserver((mutations, observer) => {
        window.setTimeout(() => {
          if (currentValue !== this.pathField.value && this.pathField.value) {
            currentValue = this.pathField.value;
            let path = this.pathField.value;
            if (path.indexOf('#') !== -1) {
              const parts = path.split('#');
              path = parts[0];
            }
            this.upload({
              path: path
            });
          }
        }, 1000);
      });
      observer.observe(this.container, {
        subtree: true,
        attributes: true
      });
    }


    // Aspect ratio - Use jQuery if we are under Joomla 3 so chosen events work
    if (econaJVersion === '3') {
      // Joomla 3 chosen event listener
      jQuery(this.aspectRatioField).on('change', () => {
        if (this.cropper) {
          this.cropper.setAspectRatio(this.aspectRatioField.value);
        }
      });
    } else {
      // Vanilla js
      this.aspectRatioField.addEventListener('change', () => {
        if (this.cropper) {
          this.cropper.setAspectRatio(this.aspectRatioField.value);
        }
      });
    }

    // Upload
    this.container.querySelector('[data-action="upload"]').addEventListener('click', (event) => {
      event.preventDefault();
      this.fileField.click();
    });

    // Media manager
    const mediaButton = this.container.querySelector('[data-action="media-manager"]');
    if (mediaButton) {
      mediaButton.addEventListener('click', (event) => {
        event.preventDefault();
        this.pathField.nextElementSibling.click();
      });
    }

    // Edit
    this.container.querySelector('[data-action="edit"]').addEventListener('click', (event) => {
      event.preventDefault();
      this.edit();
    });

    // Delete
    this.container.querySelector('[data-action="delete"]').addEventListener('click', (event) => {
      event.preventDefault();
      this.delete();
    });

    // Apply
    this.container.querySelector('[data-action="apply"]').addEventListener('click', (event) => {
      event.preventDefault();
      this.apply();
    });

    // Cancel
    this.container.querySelector('[data-action="cancel"]').addEventListener('click', (event) => {
      event.preventDefault();
      this.cancel();
    });

    // Cropper buttons
    const buttons = this.cropperButtons.querySelectorAll('[data-method]');
    for (let i = 0; i < buttons.length; i++) {
      buttons[i].addEventListener('click', (event) => {

        event.preventDefault();

        const button = event.currentTarget;
        const method = button.dataset.method;
        let option = button.dataset.option;
        let secondOption = button.dataset.secondOption;
        let direction = button.dataset.direction;
        let degrees = this.container.querySelector('input[name="econaRotateDegree"]').value;

        if (button.disabled || button.classList.contains('disabled')) {
          return;
        }

        if (this.cropper && method) {

          const data = this.cropper.getData();

          if (method === 'rotate') {

            if (data.scaleX === -1 || data.scaleY === -1) {
              direction = direction === 'left' ? 'right' : 'left';
            }

            if (direction === 'left') {
              option = '-' + degrees;
            } else {
              option = degrees;
            }

          }

          if (method === 'scaleX' && data.scaleX === -1) {
            option = 1;
          }
          if (method === 'scaleY' && data.scaleY === -1) {
            option = 1;
          }

          this.cropper[method](option, secondOption);
        }

      });
    }

  }

  upload({
    file,
    path
  }) {

    this.econa.classList.add('econa-loading');

    const data = new FormData();

    if (file) {
      data.append(this.fileField.name, file);
    }
    if (path) {
      data.append('path', path);
    }

    data.append('econaKey', this.key);
    data.append('econaUpload', this.uploadField.value);
    data.append('econaContext', this.contextField.value);
    data.append(econaSessionToken, 1);

    axios({
      method: 'post',
      url: econaBaseUrl + '/index.php?option=com_ajax&plugin=econa&group=fields&format=json',
      data: data
    }).then((response) => {
      if (response.data.success === true) {
        const image = response.data.data[0];

        this.cropperImage.setAttribute('src', image.preview + '?t=' + Date.now());
        this.previewImage.setAttribute('src', image.preview + '?t=' + Date.now());

        this.deleteField.value = '';
        this.filenameField.value = image.filename;
        this.uploadField.value = image.upload;
        this.validatorField.value = 'ok';
        this.data.width = image.width;
        this.data.height = image.height;

        this.updateValue();

        this.enableToolbarButtons();

        const autoEdit = parseInt(this.options.auto_edit, 10);
        if (autoEdit) {
          window.setTimeout(() => {
            this.container.querySelector('[data-action="edit"]').click();
          }, 1000);
        }

      } else {
        alert(response.data.message);
      }
    }).catch((error) => {
      alert(error);
    }).finally(() => {
      this.econa.classList.remove('econa-loading');
    });

  }

  apply() {

    this.econa.classList.add('econa-loading');

    const cropData = this.cropper.getData();

    const data = new FormData();
    data.append('x', cropData.x);
    data.append('y', cropData.y);
    data.append('width', cropData.width);
    data.append('height', cropData.height);
    data.append('rotate', cropData.rotate);
    data.append('scaleX', cropData.scaleX);
    data.append('scaleY', cropData.scaleY);
    data.append('task', 'process');
    data.append(econaSessionToken, 1);
    data.append('econaKey', this.key);
    data.append('econaUpload', this.uploadField.value);
    data.append('econaContext', this.contextField.value);
    data.append('fieldId', this.data.fieldId || 0);
    data.append('itemId', this.data.itemId);
    data.append('context', this.data.context);

    axios({
      method: 'post',
      url: econaBaseUrl + '/index.php?option=com_ajax&plugin=econa&group=fields&format=json',
      data: data
    }).then((response) => {
      if (response.data.success === true) {
        this.data.width = response.data.data[0].width;
        this.data.height = response.data.data[0].height;
        this.cropperImage.setAttribute('src', response.data.data[0].preview + '?t=' + Date.now());
        this.previewImage.setAttribute('src', response.data.data[0].preview + '?t=' + Date.now());
        this.uploadField.value = response.data.data[0].upload;
        this.updateValue();
        this.enableToolbarButtons();
      } else {
        alert(response.data.message);
      }
    }).catch((error) => {
      alert(error);
    }).finally(() => {
      this.closeModal();
      this.cropper.destroy();
      this.econa.classList.remove('econa-loading');
    });

  }


  edit() {
    this.openModal();
    this.setCropperWidth(this.data.width, this.data.height);
    this.cropper = new Cropper(this.cropperImage, {
      aspectRatio: this.aspectRatioField.value,
      zoomOnWheel: false,
      responsive: true,
      viewMode: 3
    });
  }


  cancel() {
    this.closeModal();
    this.cropper.destroy();
  }


  delete() {

    this.deleteField.value = '1';
    this.filenameField.value = '';
    this.validatorField.value = '';
    this.previewImage.src = econaPlaceholderImage;
    this.disableToolbarButtons();
    if (this.uploadField.value) {

      const data = new FormData();
      data.append('task', 'delete');
      data.append(econaSessionToken, 1);
      data.append('econaUpload', this.uploadField.value);
      data.append('econaContext', this.contextField.value);

      axios({
        method: 'post',
        url: econaBaseUrl + '/index.php?option=com_ajax&plugin=econa&group=fields&format=json',
        data: data
      }).then((response) => {
        if (response.data.success === true) {
          this.uploadField.value = '';
          this.updateValue();
        } else {
          alert(response.data.message);
        }
      }).catch((error) => {
        alert(error);
      });
    }
  }

  createModal() {
    if (econaJVersion === '4') {
      this.modal = new bootstrap.Modal(this.container.querySelector('.econa-modal'), {
        keyboard: false,
        backdrop: 'static'
      });
    } else {
      this.modal = jQuery(this.container).find('.econa-modal').modal({
        keyboard: false,
        backdrop: 'static'
      });
      this.closeModal();
    }

  }

  openModal() {
    if (econaJVersion === '4') {
      this.modal.show();
    } else {
      this.modal.modal('show');
    }
  }

  closeModal() {
    if (econaJVersion === '4') {
      this.modal.hide();
    } else {
      this.modal.modal('hide');
    }
  }

  enableToolbarButtons() {
    this.editButton.disabled = false;
    this.deleteButton.disabled = false;
  }

  disableToolbarButtons() {
    this.editButton.disabled = true;
    this.deleteButton.disabled = true;
  }

  setCropperWidth(width, height) {
    let value = '100%';
    if (width && height) {
      const screenWidth = window.innerWidth * 0.8;
      const screenHeight = window.innerHeight * 0.8;
      const screenRatio = screenWidth / screenHeight;
      const imageRatio = width / height;
      if (width < screenWidth && height < screenHeight) {
        value = width;
      } else if (screenRatio > imageRatio) {
        value = width * screenHeight / height;
      } else {
        value = screenWidth;
      }

      value += 'px';
    }

    this.cropperContainer.style.width = value;
  }


}

export {
  EconaCustomField as
  default
};
