import EconaArticleImages from './EconaArticleImages';
import EconaCustomField from './EconaCustomField';

const articleImages = document.querySelectorAll('.econa-article-images-container');

for (let i = 0; i < articleImages.length; i++) {
  new EconaArticleImages({
    container: articleImages[i]
  });
}

const customFields = document.querySelectorAll('.econa-field-container');

for (let i = 0; i < customFields.length; i++) {
  new EconaCustomField({
    container: customFields[i]
  });
}
